  /* src/styles/sidebar.css */
.sidebar {
  width: 250px;
  background: #1e1e2f;
  color: white;
  padding: 20px;
}

.sidebar {
    width: 250px;
    height: 100vh;
    background-color: #191d28; /* TradingView Dark Theme */
    color: white;
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
  }
  
  .sidebar a {
    color: white;
    text-decoration: none;
    padding: 10px 15px;
    display: flex;
    align-items: center;
    font-size: 16px;
  }
  
  .sidebar a:hover {
    background: #2a2d3a;
    border-radius: 5px;
  }
  
  .logout-button {
    margin-top: auto;
    background: red;
    color: white;
    padding: 10px 15px;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .sidebar {
    width: 220px;
    height: 100vh;
    background: #1e1e1e;
    color: #e0e0e0;
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
  }
  
  .sidebar h2 {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 30px;
  }
  
  .sidebar nav a {
    display: flex;
    align-items: center;
    color: #b0b0b0;
    padding: 10px;
    margin: 5px 0;
    text-decoration: none;
    transition: background 0.2s ease-in-out;
  }
  
  .sidebar nav a:hover {
    background: rgba(255, 255, 255, 0.1);
  }
  
  .sidebar .logout-button {
    background: red;
    color: white;
    border: none;
    padding: 10px;
    width: 100%;
    margin-top: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .sidebar .logout-button:hover {
    background: darkred;
  }
  

  