.dashboard-container {
    display: flex;
    min-height: 100vh;
    background: #111827;
    color: white;
  }
  
  .dashboard-content {
    flex-grow: 1;
    padding: 20px;
  }
  
  .dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logout-btn {
    background: red;
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
  }
  
  .stats-container {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  }
  
  .chart-container {
    background: #1f2937;
    padding: 20px;
    border-radius: 10px;
    margin-top: 20px;
  }
