/* üï∂Ô∏è Dark Mode */
body {
  background: #111;
  color: #fff;
  transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
}

/* üé≠ Glassmorphism */
.glass {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 20px;
  box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.1);
}

/* üöÄ Button Styles */
button {
  background: linear-gradient(45deg, #ff416c, #ff4b2b);
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  transition: transform 0.2s ease-in-out;
}

button:hover {
  transform: scale(1.1);
}

/* üî• Neon Glow Effect */
.neon-text {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  color: #ff416c;
  text-shadow: 0 0 5px #ff416c, 0 0 10px #ff4b2b, 0 0 20px #ff416c;
}

/* ‚ö° Sidebar Styles */
.sidebar {
  width: 250px;
  height: 100vh;
  background: #222;
  padding: 20px;
  position: fixed;
  left: 0;
  top: 0;
  transition: transform 0.3s ease-in-out;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li {
  padding: 15px;
  color: #ddd;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.sidebar ul li:hover {
  background: #ff416c;
  color: white;
  border-radius: 5px;
}

/* üìä Dashboard Grid */
.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-left: 270px;
  padding: 20px;
}

.card {
  padding: 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  transition: transform 0.3s ease-in-out;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.2);
}

.app-container {
  display: flex;
  height: 100vh;
}

.main-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

  