/* Removed duplicate .login-container class */
  
  .login-form {
    background-color: #ffffff;
    padding: 20px;
  }
  /* Background Animation */
@keyframes bgGradient {
    0% { background-position: left; }
    100% { background-position: right; }
  }
  
  /* Login Page */
  .login-container {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background: linear-gradient(-45deg, #ff4b5c, #ff6f61, #ff9a8d);
    background-size: 300% 300%;
    animation: bgGradient 5s infinite alternate ease-in-out;
  }
  
  /* Login Box */
  .login-box {
    background: white;
    padding: 2rem;
    border-radius: 15px;
    text-align: center;
    box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.2);
    width: 300px;
  }
  
  /* Input Fields */
  .login-box input {
    width: 90%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid #ddd;
    transition: border 0.3s;
  }
  
  .login-box input:focus {
    border-color: #ff4b5c;
  }
  
  /* Login Button */
  .login-box button {
    background: #ff4b5c;
    color: white;
    border: none;
    padding: 10px 15px;
    width: 100%;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background 0.3s, transform 0.2s;
  }
  
  .login-box button:hover {
    background: #ff1e38;
    transform: scale(1.05);
  }
